import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
// tslint:disable-next-line: max-line-length
import { AfricleanAbonnementSubscriptionComponent } from 'src/app/components/africlean-abonnement-subscription/africlean-abonnement-subscription.component';
import { AfricleanAbonnementListComponent } from 'src/app/components/africlean-abonnement-list/africlean-abonnement-list.component';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanSubscribeModel } from 'src/models/abonnement/africlean-subscription.model';
import { AfricleanHttpSubscribeProvider } from 'src/providers/africlean-http/africlean-http-subscribe.provider';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';

@Component({
  selector: 'app-abonnement',
  templateUrl: './abonnement.page.html',
  styleUrls: ['./abonnement.page.scss'],
})
export class AbonnementPage implements OnInit {

  /**
   * Gère l'affichage du text de resiliation
   */
  public displayResiliation = false;

  /**
   * Test si les abonnements en cours de validation ou de resiliation sont présent
   */
  public subscribeValidateOrResiliate = false;

  /**
   * Nombre d'abonnements souscrit par le client
   */
  public subscribes: {
    enCours: AfricleanSubscribeModel[],
    validee: AfricleanSubscribeModel[],
    enCoursResiliation: AfricleanSubscribeModel[],
    resilie: AfricleanSubscribeModel[]
  };


  constructor(private modal: AfricleanModalProvider,
              private httpSubscribe: AfricleanHttpSubscribeProvider,
              private observablesProvider: AfricleanObservablesProvider,
              private session: AfricleanSessionProvider) { }

  ngOnInit() {
    this.session.getItem('userId').then(
      (userId) => {
        const url = 'abonnement/user-subscribe/' + userId;
        this.loadSubscribes(url);
        this.observablesProvider.subscriptionRequest.subscribe(
          (isRequest) => {
            if (isRequest) {
                this.loadSubscribes(url);
            }
          }
        );
      }
    );
  }

  /**
   * Recharge les abonnements
   * @param url url 
   */
  private loadSubscribes(url: string) {
    this.httpSubscribe.getSubscribeByUser(url).subscribe(
      (subscribes) => {
        this.subscribes = subscribes;
        // Tester si les abonnements en cours de validation ou en cours de resilitaion sont présent
        if (this.subscribes.enCours.length !== 0 || this.subscribes.enCoursResiliation.length !== 0) {
            this.subscribeValidateOrResiliate = true;
        }
      }
    );
  }
  /**
   * Permet d'afficher le texte de résiliaion
   */
  public displayText() {
    console.log(this.displayResiliation)
    this.displayResiliation = true;
  }

  /**
   * Annuler la résiliation
   */
  public annuler() {
    this.displayResiliation = false;
  }

  /**
   * Permet de s'abonner en fournissant l'adresse du lieu
   */
  public async subscription() {
    this.modal.create(AfricleanAbonnementSubscriptionComponent, 'my-custom-modal-css');
  }

  /**
   * La liste des abonnements valider
   */
  public async abonnementsValidateList() {
    if (this.subscribes.validee.length !== 0) {
      this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css');
    }
  }

  /**
   * La liste des abonnements en cours de validation
   */
  public async abonnementsCurrentValidateList() {
    if (this.subscribes.enCours.length !== 0) {
      const props = {
        state: 'EN COURS'
      };
      this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css', props);
    }
  }

  /**
   * La liste des abonnements en cours de resiliation
   */
  public async abonnementsCurrentResiliationList() {
    if (this.subscribes.enCoursResiliation.length !== 0) {
      const props = {
        state: 'EN COURS DE RESILIATION'
      };
      this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css', props);
    }
  }
}
